@import '../../common.scss';

.layoutContainer {
    width: 100vw;
    height: 100vh;
    position: relative;
    display: flex;

    .mainImageContent {
        flex-grow: 1;
        height: 100%;
        overflow: hidden;
        position: relative;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;

        .mainImage {
            display: block;
            height: 96%;
            width: 100%;
            object-fit: contain;
            position: inherit;
            z-index: 2;
        }


        .mainImageBlurred {
            display: block;
            width: 100vw;
            height: 100vh;
            object-fit: cover;
            position: absolute;
            left: 0;
            top: 0;
            z-index: 1;
            filter: blur(150px);
        }

        .mainImageBackground {
            display: block;
            width: 100vw;
            height: 100vh;
            background: linear-gradient(rgba(0, 0, 0, 0.5), #19191c);
        }

        .imageRoundBtn {
            position: absolute;
            bottom: 6px;
            width: 30px;
            height: 30px;
            cursor: pointer;
            border-radius: 50%;
            background: #3b3735;
            border: none;
            transition: all 0.2s ease-in;
            z-index: 99;
            opacity: 0;

            &:hover {
                background: #4e423c;
                opacity: 1;
            }

            i {
                font-size: 25px;
                line-height: 25px;
                text-align: center;
                padding-top: 2px;
                color: $color-white;
            }
        }

        .imageRoundBtnLeft {
            left: 6px;
        }

        .imageRoundBtnRight {
            right: 6px;
        }
    }

    .mainOutlet {
        min-width: 360px;
        max-width: 360px;
        height: 100%;
        overflow: hidden;
    }

    .mainImageDescriptionContainter {
        min-width: 300px;
        max-width: 300px;
        height: 100%;
        overflow: hidden;
        background-color: #19191c;

        .mainImageDescriptionContent {
            padding: 21px;
            width: 300px;
            height: 100%;
            overflow-y: scroll;

            &::-webkit-scrollbar {
                display: none;
            }

            .main-description__share-date {
                font-family: "basier", sans-serif;
                display: block;
                font-weight: 700;
                font-size: 12px;
                line-height: 1vw;
                text-transform: uppercase;
                color: #989da7;
            }

            .main-description__title {
                font-family: "Roslindale";
                font-size: 27px;
                color: #fff;
                margin: 6px 0;
            }

            .main-description__attr {
                letter-spacing: -0.25px;
                font-size: 12px;
                color: #989da7;
                font-family: "basier", sans-serif;
                margin-bottom: 5px;
            }

            .main-description__authors {
                font-size: 12px;
                font-family: "basier", sans-serif;
                color: #fff;
                letter-spacing: -0.15px;
                list-style: none;
                margin-bottom: 5px;
                display: flex;
                align-items: center;
            }

            .main-description__author {
                background: #343434;
                padding: 4px 15px 4px 4px;
                display: inline-flex;
                justify-content: flex-start;
                align-items: center;
                border-radius: 53px;
                appearance: none;
                border: none;
                margin: 0;
                margin-right: 5px;

                img {
                    width: 25px;
                    height: 25px;
                    border-radius: 100%;
                    object-fit: fill;
                    margin-right: 5px;
                }
            }

            .main-description__text-content {
                border-top: 1px solid #474747;
                padding-bottom: 10px;
                color: #989da7;
                font-family: "basier", sans-serif;
                font-size: 12px;
                line-height: 1rem;

            }
        }

        .mainImageDescriptionFooter {
            min-width: 300px;
            max-width: 300px;
            height: 35px;
            position: absolute;
            bottom: 0;
            background: linear-gradient(0deg, rgba(25, 25, 28, 1) 0%, rgba(25, 25, 28, 0.9) 60%, rgba(25, 25, 28, 0.75) 80%, rgba(25, 25, 28, 0) 100%);
        }
    }
}