$color-boder: #343434;
$color-black: #000000;
$color-white: #ffffff;
$color-background: #070707;
$color-calendar-bg: #fffffff6;
$color-red-1: #f90000;
$color-red-2: #de0000;
$color-red-3: #ca140c;
$color-calendar-white: #f4f4f4;
$color-calendar-black: #161616;
$color-calendar-border: #16161633;
$color-calendar-text: #0000004a;




@mixin baseButton($width) {
    width: $width;
    height: round($width*19/36);
    margin-left: 1px;
    border-radius: 3px;
    outline: 0;
    border: 1px solid #7c7b7a;
    color: #ececec;
    background-color: $color-background;
    cursor: pointer;
    transition: .2s ease;
    display: flex;
    align-items: center;
    justify-content: center;

    i {
        font-size: round($width*15/36);
        line-height: round($width*19/36);
    }

    &:hover {
        color: $color-white;
    }

    &:disabled {
        opacity: 0.3;
    }
}

@mixin baseBody() {
    font-family: "nyt-imperial";
    font-weight: 400;
    font-style: normal;
    font-size: 12px;
    padding: 3px;
    margin-bottom: 3px;
    background: url(./assets/main/paper.jpeg);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
}

@mixin myInput() {
    height: 25px;
    font-family: "basier", sans-serif;
    color: $color-white;
    font-size: 11px;
    padding: 0 9px;
    background: #0c0c0c;
    border: 0;
    outline: 0;
}


// -------------------RESPONSIVE START-------------------- //
@media only screen and (max-width: 768px) {

    .mainImageContent {
        display: flex;
        justify-content: center;
        background-color: $color-black;

        .subOutletLeft {
            flex-grow: 1;
            border-right: 1px solid $color-boder;
        }

        .subOutletRight {
            flex-grow: 1;
            border-left: 1px solid $color-boder;
        }

    }
}